












import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component({})
export default class Confirm extends Vue {
  showflag = false;

  @Prop({ default: "是否全部删除？" })
  title!: string;
  @Prop({ default: "取消" })
  cancleBtnText!: string;
  @Prop({ default: "确定" })
  enterBtnText!: string;

  show() {
    this.showflag = true;
  }
  hide() {
    this.showflag = false;
  }
  clickCancleBtn() {
    this.$emit("cancle");
    this.hide();
  }
  clickEnterBtn() {
    this.$emit("enter");
    this.hide();
  }
}
