


























import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component
export default class ProgressCircle extends Vue {
  dasharray = Math.PI * 2 * 17;

  @Prop({ default: 0 })
  percent!: number;
  @Prop({ default: 0 })
  radius!: number;

  get dashoffset() {
    return this.dasharray * (1 - this.percent);
  }
}
