








import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component({})
export default class TopTip extends Vue {
  showflag = false;
  timer = 0;
  show() {
    this.showflag = true;
    window.clearTimeout(this.timer);
    this.timer = window.setTimeout(() => {
      this.hide();
    }, 1000);
  }
  hide() {
    this.showflag = false;
  }
}
